#root {
  display: grid;
  grid-template-columns: repeat(12, 8%);
  grid-template-rows: repeat(24, 5%);
  background-color: rgb(34, 52, 34);
}

header {
  display: grid;
  background-color: white;
  width: 97%;
  border-radius: 25px;
  grid-area: 23 1 24 12;
}

footer {
  display: grid;
  background-color: white;
  width: 97%;
  border-radius: 25px;
  grid-column: 1 1;
  grid-row: 23 24;
}

article {
  display: grid;
}

img {
  border-radius: 25px;
  justify-self: center;
  align-self: center;
  object-fit: cover;
  width: 97%;
  height: 97%;
}

.centerimage {
  grid-area: 1 / 6 / 5 / 10;
  z-index: 3;
  transition: transform 2s ease;
}

@keyframes shrinkAndMove {
  0% {
    transform: scale(1.2) translateX(-100px);
  }
  50% {
    transform: scale(1) translateX(-100px);
  }
  100% {
    transform: scale(1) translateX(0);
  }
}

.centerimage {
  animation: shrinkAndMove 2s ease-out 1s forwards;
}

.projet2 {
  grid-area: 14 / 6 / 23 / 10;

  transition: transform 2s ease;
}

@keyframes expand2 {
  0% {
    transform: scale(0) translateX(0);
  }
  50% {
    transform: scale(0) translateX(0);
  }
  100% {
    transform: scale(1) translateX(0) translateY(0);
  }
}

.projet2 {
  animation: expand2 2s ease-out 1s forwards;
}

